//
const login = value => {
	return {
		type: "ISLOGIN",
		value
	};
};

//
const shrinkLeftMenu = value => {
	return {
		type: "SHRINKLEFTMENU",
		value
	};
};

//
const setLanguage = value => {
	return {
		type: "SETLANGUAGE",
		value
	};
};

//
const setAlertData = value => {
	return {
		type: "SETALERTDATA",
		value
	};
};

//
const setMsgData = value => {
	return {
		type: "SETMSGDATA",
		value
	};
};

export { login, shrinkLeftMenu, setLanguage, setAlertData, setMsgData };
