//
const isLoginReducer = (state = false, action) => {
	switch (action.type) {
		case "ISLOGIN":
			return action.value;
		default:
			return state;
	}
};

//
const shrinkLeftMenuReducer = (state = false, action) => {
	switch (action.type) {
		case "SHRINKLEFTMENU":
			return action.value;
		default:
			return state;
	}
};

//
const setLanguageReducer = (state = "CN", action) => {
	switch (action.type) {
		case "SETLANGUAGE":
			return action.value;
		default:
			return state;
	}
};

//
const setAlertDataReducer = (state = [], action) => {
	switch (action.type) {
		case "SETALERTDATA":
			return action.value;
		default:
			return state;
	}
};

//
const setMsgDataReducer = (state = [], action) => {
	switch (action.type) {
		case "SETMSGDATA":
			return action.value;
		default:
			return state;
	}
};

export {
	isLoginReducer,
	shrinkLeftMenuReducer,
	setLanguageReducer,
	setAlertDataReducer,
	setMsgDataReducer
};
