const getButtonByType = type => {
	switch (type) {
		// 
		case "approval":
			return approval;
		// 
		case "back":
			return back;
		// 
		case "cancel":
			return cancel;
		// 
		case "disable":
			return disable;
		// 
		case "enable":
			return enable;
		// 
		case "file":
			return file;
		// 
		case "history":
			return history;
		// 
		case "info":
			return info;
		// 
		case "print":
			return print;
		// 
		case "query":
			return query;
		// 
		case "save":
			return save;
		// 
		case "submit":
			return submit;
		// 
		case "upload":
			return upload;
		// 
		case "view":
			return view;
		// 
		case "withdraw":
			return withdraw;
		default:
			return approval;
	}
};

// 
// 
// 
class Button extends React.Component {
	render() {
		const { onClick, type, num, text, disabled } = this.props;
		return (
			<li
				onClick={() => {
					if (typeof onClick === "function" && !disabled) onClick();
				}}
			>
				<div
					className={
						disabled
							? `com-btn disabled`
							: num
							? `com-btn num${num}`
							: `com-btn`
					}
				>
					<img alt="btn" src={getButtonByType(type)} />
					<span>{text}</span>
				</div>
			</li>
		);
	}
}

export default Button;
