import React, { Fragment } from "react";
import PageTitle from "../../../components/PageTitle";
import Breadcrumb from "../../../components/Breadcrumb";
import Table from "../../../components/Table";
import TopBar from "../../../components/TopBar";
import Button from "../../../components/Btns";

import dao from "./dao";

const { TopBarLeft, TopBarRight } = TopBar;
const { Select, Input, DateRange } = TopBar;

// 
const colString =
	"														";

const columns = colString.split("	").map((item, i) => {
	return { title: `${item}`, dataIndex: `string${i}`, key: `c1key${i}` };
});

class Default extends React.Component {
	state = {
		dataSource: []
	};

	componentDidMount() {
		this.refs.table.loading();
		dao.getTable().then(res => {
			this.setState({
				dataSource: res
			});
			if (this.refs.table) this.refs.table.loaded();
		});
	}

	render() {
		const { dataSource } = this.state;
		return (
			<Fragment>
				<PageTitle title="" />
				<Breadcrumb />
				<div className="page-container">
					<TopBar>
						<TopBarLeft ref="conditions">
							<DateRange label={""} field="string0" />
							<Select
								label={""}
								field="string1"
								data={[
									{ label: "1", value: "state1" },
									{ label: "2", value: "state2" },
									{ label: "3", value: "state3" }
								]}
							/>
							<Input label={""} field="string2" />
							<Input label={""} field="string3" />
							<Input label={""} field="string4" />
							<Input label={""} field="string5" />
							<Select
								label={""}
								field="string6"
								data={[
									{ label: "1", value: "unit1" },
									{ label: "2", value: "unit2" },
									{ label: "3", value: "unit3" }
								]}
							/>
						</TopBarLeft>
						<TopBarRight>
							<Button
								type="query"
								num={1}
								text=""
								onClick={this.onClickQuery}
							/>
							<Button type="edit" num={2} text="" />
							<Button type="scanjy" num={3} text="" />
							<Button type="jyconfirm" num={4} text="" />
							<Button type="cancel" num={5} text="" />
							<Button type="myjy" num={6} text="" />
							<Button type="back" num={7} text="" />
							<Button type="print" num={8} text="" />
						</TopBarRight>
					</TopBar>
					<Table
						ref="table"
						columns={columns}
						dataSource={dataSource}
						selectable
					/>
				</div>
			</Fragment>
		);
	}
}

export default Default;
