import React, { Fragment } from "react";
import PageTitle from "../../../components/PageTitle";
import Breadcrumb from "../../../components/Breadcrumb";
import TopBar from "../../../components/TopBar";
import Button from "../../../components/Btns";
import Table from "../../../components/Table";

import dao from "./dao";

const { TopBarLeft, TopBarRight } = TopBar;
const { Select, Input, DatePicker } = TopBar;

// 
const columns = [
	"",
	"",
	"",
	"",
	"",
	"",
	"",
	"",
	"",
	"",
	"",
	"",
	"",
	"",
	"",
	"",
	"",
	"",
	""
].map((item, i) => {
	return { key: `key${i}`, title: `${item}`, dataIndex: `string${i}` };
});

class Default extends React.Component {
	state = {
		dataSource: []
	};

	componentDidMount() {
		this.refs.table.loading();
		dao.getTable().then(res => {
			this.setState({
				dataSource: res
			});
			if (this.refs.table) this.refs.table.loaded();
		});
	}

	render() {
		const { dataSource } = this.state;
		return (
			<Fragment>
				<PageTitle title="" />
				<Breadcrumb />
				<div className="page-container">
					<TopBar>
						<TopBarLeft ref="conditions">
							<DatePicker label={""} field="string5" />
							<Select label={""} field="string0" />
							<Input label={""} field="string1" />
							<Input label={""} field="string2" />
							<Select label={""} field="string3" />
							<Input label={""} field="string4" />
						</TopBarLeft>
						<TopBarRight>
							<Button
								type="query"
								num={1}
								text=""
								onClick={this.onClickQuery}
							/>
							<Button type="add" num={2} text="" />
							<Button type="edit" num={4} text="" />
							<Button type="confirm" num={5} text="" />
							<Button type="edit" num={6} text="" />
							<Button type="cancel" num={7} text="" />
						</TopBarRight>
					</TopBar>
					<Table ref="table" columns={columns} dataSource={dataSource} />
				</div>
			</Fragment>
		);
	}

	onClickQuery = () => {
		let conditions = this.refs.conditions.getConditions();
		console.log(conditions);
	};
}

export default Default;
